import { BsDropdownModule } from 'ng2-bootstrap/dropdown';
import { CurrencyMaskModule } from 'ng2-currency-mask';
import { SelectModule } from 'ng2-select';
import { UnidadeMedidaService } from './../unidademedida/unidade-medida.service';
import { CategoriaService } from './../categoria/categoria.service';

import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { ModalModule } from 'ng2-bootstrap/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { SharedComponentModule } from './../../../shared/shared-component.module';

import { AdicionalProdutoService } from './adicional-produto.service';
import { AdicionalProdutoRoutingModule } from './adicional-produto-routing.module';
import { AdicionalProdutoPopupModalComponent } from './popup/adicional-produto-popup-modal.component';
import { ListarAdicionalProdutoComponent } from './listar/listar-adicional-produto.component';
import { ProdutoService } from '../../cadastros/produto/produto.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    BsDropdownModule,
    ModalModule.forRoot(),
    SelectModule,
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    AdicionalProdutoRoutingModule,
    CurrencyMaskModule
  ],
  declarations: [
    ListarAdicionalProdutoComponent,
    AdicionalProdutoPopupModalComponent
  ],
  providers:[
    ProdutoService,
    CategoriaService,
    UnidadeMedidaService,
    AdicionalProdutoService
  ]
})
export class AdicionalProdutoModule { }



