import { ListarAdicionalProdutoComponent } from './listar/listar-adicional-produto.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { create } from 'domain';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'adicionais por categoria'
    },
    children: [
      {
        path: 'listar',
        component: ListarAdicionalProdutoComponent,
        data: {
          title: 'Listar'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdicionalProdutoRoutingModule { }



