import { TrataErrorService } from 'services/trata-error.service';
import { UnidadeMedidaService } from './../../unidademedida/unidade-medida.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter, Input } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { ProdutoService } from '../../../cadastros/produto/produto.service';
import { CategoriaService } from '../../categoria/categoria.service';

import { ComboDTO } from 'models/domain/combo.dto';
import { AdicionalProdutoService } from './../adicional-produto.service';
import { AdicionalCategoriaDTO } from './../../../../../models/domain/adicional-categoria.dto';

@Component({
  selector: 'adicional-produto-popup-modal',
  templateUrl: './adicional-produto-popup-modal.component.html',
  styleUrls: ['./adicional-produto-popup-modal.component.scss']

})
export class AdicionalProdutoPopupModalComponent extends AbstractPopupComponent implements OnInit {


  item: AdicionalCategoriaDTO;
  comboCategorias: ComboDTO[] = []
  comboUnidades: ComboDTO[] = []
  comboProdutos: ComboDTO[] = []


  constructor(
    public adicionalService: AdicionalProdutoService,
    public produtoService: ProdutoService,
    public categoriaService: CategoriaService,
    public unidadeService: UnidadeMedidaService,
    public fb: FormBuilder) {
    super('adicionalPopup');
  }

  ngOnInit() {
    this.criarForm();
    this.carregarCombos();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra adicionais por categoria");
  }

  showAlteracao(item: AdicionalCategoriaDTO) {
    this.alterarForm(item)
    this.showPopup("Cadastra adicionais por categoria");

  }

  btnConfirmar(item: any) {
    let adic = {} as AdicionalCategoriaDTO;
    adic.id = item.id;
    adic.idCategoria = item.categoria[0].id;
    adic.idProduto = item.produto[0].id;
    adic.idUnidadeMedida = item.unidade[0].id;
    adic.qtUtilizada = item.qtUtilizada;
    adic.emAlteracao = item.emAlteracao;
    adic.vlAdicional = item.vlAdicional;
    this.acao(adic, this.adicionalService);
  }

  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      categoria: ['', Validators.required],
      produto: [''],
      unidade: [''],
      qtUtilizada: [''],
      status: ['A'],
      vlAdicional:[''],
      emAlteracao: [false]
    })
  }

  alterarForm(item: AdicionalCategoriaDTO) {
    this.formulario.patchValue({
      id: item.id,
      categoria: [{ id: item.idCategoria, text: item.nmCategoria }],
      produto: [{ id: item.idProduto, text: item.nmProduto }],
      unidade: [{ id: item.idUnidadeMedida, text: item.nmUnidadeMedida }],
      qtUtilizada: item.qtUtilizada,
      vlAdicional: item.vlAdicional,
      status: item.status,
      emAlteracao: [true]
    })
  }

  carregarCombos() {
    this.categoriaService.findCategoriasAtivasExcetoInterna("PRD")
      .subscribe(
        response => this.comboCategorias = this.preencharCombo(response, 'id', 'nmCategoria'),
        error => TrataErrorService.tratarError(error)
      );

    this.produtoService.findAllProdutoInterno()
      .subscribe(
        response => this.comboProdutos = this.preencharCombo(response, 'id', 'nmProduto'),
        error => TrataErrorService.tratarError(error)
      )

    this.unidadeService.findAllAtivo()
      .subscribe(
        response => this.comboUnidades = this.preencharCombo(response, 'id', 'sgUnidadeMedida'),
        error => TrataErrorService.tratarError(error)
      )
  }
}



