import { AdicionalProdutoService } from './../adicional-produto.service';
import { AdicionalCategoriaDTO } from './../../../../../models/domain/adicional-categoria.dto';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';


import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { UnidadeMedidaDTO } from './../../../../../models/domain/unidade-medida.dto';
import { InfoNutricionalDTO } from 'models/domain/info-nutricional.dto';
import { TrataErrorService } from 'services/trata-error.service';


@Component({
  templateUrl: './listar-adicional-produto.component.html',
  styleUrls: ['./listar-adicional-produto.component.scss']
})
export class ListarAdicionalProdutoComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeSatusModal') popupChangeStatus;

  item: AdicionalCategoriaDTO;
  items: AdicionalCategoriaDTO[];

  constructor(
    public adicionalService: AdicionalProdutoService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos();
  }

  selecionarItem(item: AdicionalCategoriaDTO) {
    this.item = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: AdicionalCategoriaDTO) {
    this.item = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.adicionalService.changeStatus(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Adicional por categoria', 'O status do adicional foi alterado com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.adicionalService.excluir(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Adicional por categoria', 'O adicional da categoria foi excluído com sucesso!!!')
        this.carregarTodos()
      }, error => TrataErrorService.tratarError(error));
  }

  private carregarTodos() {
    this.adicionalService.findAll().subscribe(response => {
      this.items = response
    }, error => TrataErrorService.tratarError(error));
  }

}



